@import url(https://fonts.googleapis.com/css?family=Cardo);
.gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
    grid-gap: 1rem;
    max-width: 80rem;
    margin-left: 290px;
    margin-right: 40px;
    margin-top: 40px;
    padding-left: 30px;
/*    margin: 5rem auto;*/
    /*padding: 0 5rem;*/
}
.gallery-panel img {
    width: 100%;
    height: 16vw;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 0.75rem;
}

.menu {
  width: 250px;
  position: fixed;
  font-family: 'Cardo';
  font-weight: bold;
  margin-left: 40px;
}
.link {
  text-decoration: none;
  font-size: 1.5em;
  color: #000000;
  text-align: center;
}
.link_container {
  margin-top: 40px;
  margin-bottom: 40px;
}

.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}
.lightbox img {
  margin: auto;
  width: 100%;
  grid-column-start: 2;
}
.lightbox-info {
  margin: auto 2rem auto 0;
}
.lightbox-info-inner {
  background-color: #FFFFFF;
  display: inline-block;
  padding: 2rem;
}
